import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

// Components
import Home from "./pages/Home";
import Contact from "./pages/Contact";
import About from "./pages/About";
import Hire from "./pages/Hire";
import Projects from "./pages/Projects";

const DEFAULT = "Ubaid Maalik";

const routes = [
  { path: "/", component: Home, meta: { title: `${DEFAULT} - Home` } },
  {
    path: "/index.html",
    component: Home,
    meta: { title: `${DEFAULT} - Home` },
  },
  {
    path: "/contact-me",
    component: Contact,
    meta: { title: `${DEFAULT} - Contact Me` },
  },

  {
    path: "/about-me",
    component: About,
    meta: { title: `${DEFAULT} - About Me` },
  },

  {
    path: "/hire-me",
    component: Hire,
    meta: { title: `${DEFAULT} - Hire Me` },
  },

  {
    path: "/projects",
    component: Projects,
    meta: { title: `${DEFAULT} - My Projects` },
  },
];

const router = new VueRouter({ mode: "history", routes });

export default router;
