<template>
  <section class="contact-form-wrapper">
    <div class="left-title">
      <h1>Contact Me</h1>
      <p>Leave a message for me, I'll reply ASAP</p>
    </div>

    <div class="right-form">
      <form autocomplete="off" v-on:submit.prevent="handleSubmit">
        <div class="form-group">
          <label for="name">Name</label>
          <input
            type="text"
            name="name"
            id="name"
            class="form-control"
            placeholder="Your Name"
            v-model.trim="$v.name.$model"
            @input="$v.name.$touch()"
            required
          />
        </div>
        <div v-if="$v.name.$error">
          <div class="error" v-if="!$v.name.required">Field is required</div>
        </div>

        <div class="form-group">
          <label for="email">Email</label>
          <input
            type="email"
            name="email"
            id="email"
            class="form-control"
            placeholder="Your Email"
            v-model.trim="$v.email.$model"
            @input="$v.email.$touch()"
            required
          />
          <div class="error" v-if="!$v.email.email">The field must a valid email</div>
          <div v-if="$v.email.$error">
            <div class="error" v-if="!$v.email.required">The field is required</div>
          </div>
        </div>

        <div class="form-group">
          <label for="subject">Subject</label>
          <input
            type="text"
            name="subject"
            id="subject"
            class="form-control"
            placeholder="Message Subject"
            v-model.trim="$v.subject.$model"
            @input="$v.subject.$touch()"
            required
          />

          <div v-if="$v.subject.$error">
            <div class="error" v-if="!$v.subject.required">The field is required</div>
          </div>
        </div>

        <div class="form-group">
          <label for="name">Message</label>
          <textarea
            name="message"
            id="message"
            class="form-control"
            placeholder="Your Message"
            v-model.trim="$v.message.$model"
            @input="$v.message.$touch()"
            required
          ></textarea>

          <div v-if="$v.message.$error">
            <div class="error" v-if="!$v.message.required">The field is required</div>
          </div>
        </div>

        <div class="form-group">
          <input
            type="submit"
            class="btn btn-primary"
            value="SEND MESSAGE"
            :class="{ disabled: requestPending }"
          />
        </div>
      </form>

      <div class="alert success-alert" v-if="success">Your message has been sent</div>
      <div class="alert danger-alert" v-if="error">Message could not be sent. Something went wrong</div>
    </div>
  </section>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import axios from "axios";

export default {
  data() {
    return {
      error: false,
      success: false,
      requestPending: false,
      name: "",
      email: "",
      subject: "",
      message: ""
    };
  },
  validations: {
    name: {
      required
    },
    email: {
      required,
      email
    },
    subject: {
      required
    },
    message: {
      required
    }
  },

  methods: {
    async handleSubmit() {
      this.requestPending = true;

      const data = `name=${this.name}&email=${this.email}&subject=${this.subject}&message=${this.message}`;

      try {
        await axios.post("https://siyabdev.com/mail.php", data, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
          }
        });

        this.requestPending = false;

        this.success = true;

        // Clear the fields
        this.name = "";
        this.email = "";
        this.subject = "";
        this.message = "";
      } catch (error) {
        this.error = true;
        console.error(error);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../styles/contact.scss";
</style>
